<template>
  <div class="keywords-page">
    <div class="page-header">
      <h1>关键词管理</h1>
      
      <div class="header-actions">
        <el-button type="primary" @click="refreshKeywords" class="apple-btn">
          <el-icon><RefreshIcon /></el-icon>
          刷新
        </el-button>
        
        <el-popover
          placement="bottom-end"
          :width="300"
          trigger="click"
          popper-class="filter-popover"
        >
          <template #reference>
            <el-button class="filter-btn" :class="{ 'is-active': hasActiveFilters }">
              <el-icon><Filter /></el-icon>
              <span>筛选</span>
              <el-badge v-if="hasActiveFilters" :value="1" class="filter-badge" />
            </el-button>
          </template>
          
          <!-- 筛选内容 -->
          <div class="filter-content">
            <h4 class="filter-title">筛选关键词</h4>
            <el-form :model="filter">
              <el-form-item label="分类">
                <el-select v-model="filter.categoryId" placeholder="全部分类" clearable class="full-width">
                  <el-option label="全部分类" value=""></el-option>
                  <el-option
                    v-for="category in categories"
                    :key="category.id"
                    :label="`${category.name} (${category.keyword_count})`"
                    :value="category.id"
                  ></el-option>
                </el-select>
              </el-form-item>
              
              <div class="filter-actions">
                <el-button type="primary" @click="filterKeywords">应用筛选</el-button>
                <el-button @click="resetFilter" plain>重置</el-button>
              </div>
            </el-form>
            
            <el-divider />
            
            <div class="category-actions">
              <h4 class="filter-title">分类管理</h4>
              <el-button 
                type="danger" 
                @click="confirmDeleteCategory" 
                :disabled="!filter.categoryId"
                class="delete-category-btn"
              >
                <el-icon><Delete /></el-icon>
                删除当前分类
              </el-button>
            </div>
          </div>
        </el-popover>
      </div>
    </div>
    
    <!-- 上传关键词区域 -->
    <div class="upload-section">
      <div class="section-header">
        <h2>添加关键词</h2>
        <el-button 
          @click="isUploadVisible = !isUploadVisible" 
          class="toggle-btn" 
          :class="{ 'is-active': isUploadVisible }"
        >
          <el-icon v-if="isUploadVisible"><ArrowUp /></el-icon>
          <el-icon v-else><ArrowDown /></el-icon>
          {{ isUploadVisible ? '收起' : '展开' }}
        </el-button>
      </div>
      
      <div class="upload-content" v-show="isUploadVisible">
        <el-tabs v-model="activeTab">
          <!-- 手动添加关键词 -->
          <el-tab-pane label="手动添加" name="manual">
            <el-form :model="keywordForm" label-width="80px">
              <el-form-item label="分类">
                <div class="category-select-wrapper">
                  <el-select v-model="keywordForm.categoryId" placeholder="请选择分类" class="category-select">
                    <el-option
                      v-for="category in categories"
                      :key="category.id"
                      :label="category.name"
                      :value="category.id"
                    ></el-option>
                  </el-select>
                  <el-button type="primary" @click="showAddCategoryDialog" class="add-category-btn" plain>
                    <el-icon><Plus /></el-icon>
                    添加分类
                  </el-button>
                </div>
              </el-form-item>
              
              <el-form-item label="关键词">
                <el-input 
                  v-model="keywordForm.keyword"
                  type="textarea" 
                  :rows="3" 
                  placeholder="请输入关键词，每行一个关键词" 
                  class="keyword-input"
                ></el-input>
              </el-form-item>
              
              <el-form-item>
                <el-button type="primary" @click="addKeywords" :loading="loading">添加关键词</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          
          <!-- Excel导入 -->
          <el-tab-pane label="Excel导入" name="excel">
            <el-form :model="excelForm" label-width="80px">
              <el-form-item label="分类">
                <div class="category-select-wrapper">
                  <el-select v-model="excelForm.categoryId" placeholder="请选择分类" class="category-select">
                    <el-option
                      v-for="category in categories"
                      :key="category.id"
                      :label="category.name"
                      :value="category.id"
                    ></el-option>
                  </el-select>
                  <el-button type="primary" @click="showAddCategoryDialog" class="add-category-btn" plain>
                    <el-icon><Plus /></el-icon>
                    添加分类
                  </el-button>
                </div>
              </el-form-item>
              
              <el-form-item label="Excel文件">
                <el-upload
                  class="excel-uploader"
                  action="#"
                  :auto-upload="false"
                  :on-change="handleExcelChange"
                  :file-list="excelForm.fileList"
                  :limit="1"
                  accept=".xlsx,.xls,.csv"
                >
                  <el-button type="primary">选择Excel文件</el-button>
                  <template #tip>
                    <div class="el-upload__tip">请上传Excel文件，第一列将被识别为关键词</div>
                  </template>
                </el-upload>
              </el-form-item>
              
              <el-form-item>
                <el-button type="primary" @click="uploadExcel" :loading="loading" :disabled="!excelForm.file">
                  上传并导入
                </el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    
    <!-- 标签筛选区 -->
    <div class="category-tags">
      <el-tag 
        :effect="filter.categoryId === '' ? 'dark' : 'plain'"
        class="category-tag" 
        @click="selectCategory('')"
      >
        全部关键词
      </el-tag>
      
      <el-tag 
        v-for="category in categories" 
        :key="category.id"
        :effect="filter.categoryId === category.id ? 'dark' : 'plain'"
        class="category-tag" 
        @click="selectCategory(category.id)"
      >
        {{ category.name }} ({{ category.keyword_count }})
      </el-tag>
    </div>
    
    <!-- 关键词列表区域 -->
    <div v-loading="loading" class="keywords-list">
      <el-empty v-if="keywords.length === 0" description="暂无关键词">
        <template #image>
          <el-icon class="empty-icon"><DocumentRemove /></el-icon>
        </template>
        <el-button type="primary" @click="isUploadVisible = true">添加关键词</el-button>
      </el-empty>
      
      <div v-else>
        <el-table :data="keywords" style="width: 100%">
          <el-table-column prop="id" label="ID" width="80" />
          <el-table-column prop="keyword" label="关键词" min-width="200" />
          <el-table-column label="分类" width="180">
            <template #default="scope">
              <el-tag size="small" effect="plain">
                {{ getCategoryName(scope.row.category_id) }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="创建时间" width="180">
            <template #default="scope">
              {{ formatDate(scope.row.created_at) }}
            </template>
          </el-table-column>
          <el-table-column label="操作" width="150">
            <template #default="scope">
              <el-button type="primary" size="small" @click="editKeyword(scope.row)" plain>编辑</el-button>
              <el-button type="danger" size="small" @click="confirmDeleteKeyword(scope.row)" plain>删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        
        <!-- 分页 -->
        <div class="pagination-container">
          <el-pagination
            v-model:current-page="pagination.page"
            v-model:page-size="pagination.limit"
            :page-sizes="[20, 50, 100, 200]"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total"
            @size-change="handleSizeChange"
            @current-change="handlePageChange"
            background
          />
        </div>
      </div>
    </div>
    
    <!-- 添加分类对话框 -->
    <el-dialog
      v-model="categoryDialog.visible"
      :title="categoryDialog.isEdit ? '编辑分类' : '添加分类'"
      width="500px"
      destroy-on-close
    >
      <el-form :model="categoryDialog.form" label-width="80px">
        <el-form-item label="分类名称" required>
          <el-input v-model="categoryDialog.form.name" placeholder="请输入分类名称"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="categoryDialog.visible = false">取消</el-button>
          <el-button type="primary" @click="saveCategory" :loading="categoryDialog.loading">
            {{ categoryDialog.isEdit ? '更新' : '添加' }}
          </el-button>
        </span>
      </template>
    </el-dialog>
    
    <!-- 编辑关键词对话框 -->
    <el-dialog
      v-model="keywordDialog.visible"
      title="编辑关键词"
      width="500px"
      destroy-on-close
    >
      <el-form :model="keywordDialog.form" label-width="80px">
        <el-form-item label="分类">
          <el-select v-model="keywordDialog.form.categoryId" placeholder="请选择分类" class="full-width">
            <el-option
              v-for="category in categories"
              :key="category.id"
              :label="category.name"
              :value="category.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关键词" required>
          <el-input v-model="keywordDialog.form.keyword" placeholder="请输入关键词"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="keywordDialog.visible = false">取消</el-button>
          <el-button type="primary" @click="updateKeyword" :loading="keywordDialog.loading">
            更新
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup name="KeywordsManagement">
import { ref, computed, onMounted, watch } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { 
  ArrowUp, 
  ArrowDown, 
  Filter, 
  Delete, 
  DocumentRemove, 
  Plus, 
  RefreshRight as RefreshIcon 
} from '@element-plus/icons-vue'
import axios from '@/utils/axios'

// 页面状态
const loading = ref(false)
const isUploadVisible = ref(true)
const activeTab = ref('manual')
const keywords = ref([])
const categories = ref([])

// 分页
const pagination = ref({
  page: 1,
  limit: 20,
  total: 0
})

// 筛选条件
const filter = ref({
  categoryId: ''
})

const hasActiveFilters = computed(() => {
  return filter.value.categoryId !== ''
})

// 关键词表单
const keywordForm = ref({
  categoryId: '',
  keyword: ''
})

// Excel上传表单
const excelForm = ref({
  categoryId: '',
  file: null,
  fileList: []
})

// 分类对话框
const categoryDialog = ref({
  visible: false,
  isEdit: false,
  loading: false,
  form: {
    id: null,
    name: ''
  }
})

// 关键词对话框
const keywordDialog = ref({
  visible: false,
  loading: false,
  form: {
    id: null,
    categoryId: '',
    keyword: ''
  }
})

// 初始化
onMounted(async () => {
  await Promise.all([
    loadCategories(),
    loadKeywords()
  ])
})

// 监听分类变化，自动更新表单中的分类
watch(() => categories.value, () => {
  if (categories.value.length > 0 && !keywordForm.value.categoryId) {
    keywordForm.value.categoryId = categories.value[0].id
    excelForm.value.categoryId = categories.value[0].id
  }
}, { immediate: true })

// 加载分类
async function loadCategories() {
  try {
    const response = await axios.get('/api/keywordCategories')
    if (!response.data.error) {
      categories.value = response.data.data
    }
  } catch (error) {
    console.error('加载分类失败:', error)
    ElMessage.error('加载分类失败')
  }
}

// 加载关键词
async function loadKeywords() {
  loading.value = true
  try {
    const params = {
      limit: pagination.value.limit,
      offset: (pagination.value.page - 1) * pagination.value.limit
    }
    
    if (filter.value.categoryId) {
      params.category_id = filter.value.categoryId
    }
    
    const response = await axios.get('/api/keywords', { params })
    if (!response.data.error) {
      keywords.value = response.data.data.keywords
      pagination.value.total = response.data.data.pagination.total
    }
  } catch (error) {
    console.error('加载关键词失败:', error)
    ElMessage.error('加载关键词失败')
  } finally {
    loading.value = false
  }
}

// 刷新关键词
function refreshKeywords() {
  loadKeywords()
}

// 筛选关键词
function filterKeywords() {
  pagination.value.page = 1
  loadKeywords()
}

// 重置筛选
function resetFilter() {
  filter.value.categoryId = ''
  pagination.value.page = 1
  loadKeywords()
}

// 选择分类
function selectCategory(categoryId) {
  filter.value.categoryId = categoryId
  pagination.value.page = 1
  loadKeywords()
}

// 处理页码变化
function handlePageChange(page) {
  pagination.value.page = page
  loadKeywords()
}

// 处理每页数量变化
function handleSizeChange(size) {
  pagination.value.limit = size
  pagination.value.page = 1
  loadKeywords()
}

// 显示添加分类对话框
function showAddCategoryDialog() {
  categoryDialog.value.isEdit = false
  categoryDialog.value.form = {
    id: null,
    name: ''
  }
  categoryDialog.value.visible = true
}

// 保存分类
async function saveCategory() {
  if (!categoryDialog.value.form.name) {
    ElMessage.warning('请输入分类名称')
    return
  }
  
  categoryDialog.value.loading = true
  try {
    let response
    if (categoryDialog.value.isEdit) {
      // 更新分类
      response = await axios.put(`/api/keywordCategories/${categoryDialog.value.form.id}`, {
        name: categoryDialog.value.form.name
      })
    } else {
      // 创建分类
      response = await axios.post('/api/keywordCategories', {
        name: categoryDialog.value.form.name
      })
    }
    
    if (!response.data.error) {
      ElMessage.success(categoryDialog.value.isEdit ? '分类更新成功' : '分类创建成功')
      categoryDialog.value.visible = false
      await loadCategories()
    }
  } catch (error) {
    console.error('保存分类失败:', error)
    ElMessage.error('保存分类失败')
  } finally {
    categoryDialog.value.loading = false
  }
}

// 确认删除分类
function confirmDeleteCategory() {
  if (!filter.value.categoryId) return
  
  const category = categories.value.find(c => c.id === filter.value.categoryId)
  if (!category) return
  
  // 检查分类是否包含关键词
  const hasKeywords = category.keyword_count > 0
  
  ElMessageBox.confirm(
    hasKeywords 
      ? `分类 "${category.name}" 包含 ${category.keyword_count} 个关键词，确定要删除该分类及其所有关键词吗？`
      : `确定要删除分类 "${category.name}" 吗？`,
    '删除确认',
    {
      confirmButtonText: '删除',
      cancelButtonText: '取消',
      type: 'warning'
    }
  ).then(() => {
    deleteCategory(category.id, hasKeywords)
  }).catch(() => {})
}

// 删除分类
async function deleteCategory(categoryId, deleteKeywords = false) {
  loading.value = true
  try {
    const response = await axios.delete(`/api/keywordCategories/${categoryId}`, {
      params: { deleteKeywords: deleteKeywords }
    })
    
    if (!response.data.error) {
      ElMessage.success(response.data.message || '分类删除成功')
      await loadCategories()
      if (filter.value.categoryId === categoryId) {
        filter.value.categoryId = ''
      }
      await loadKeywords()
    }
  } catch (error) {
    console.error('删除分类失败:', error)
    ElMessage.error('删除分类失败')
  } finally {
    loading.value = false
  }
}

// 添加关键词
async function addKeywords() {
  if (!keywordForm.value.categoryId) {
    ElMessage.warning('请选择分类')
    return
  }
  
  if (!keywordForm.value.keyword) {
    ElMessage.warning('请输入关键词')
    return
  }
  
  loading.value = true
  try {
    // 将多行关键词分割为数组
    const keywords = keywordForm.value.keyword.split('\n')
      .map(k => k.trim())
      .filter(k => k)
    
    if (keywords.length === 0) {
      ElMessage.warning('请输入有效的关键词')
      loading.value = false
      return
    }
    
    if (keywords.length === 1) {
      // 单个关键词
      const response = await axios.post('/api/keywords', {
        category_id: keywordForm.value.categoryId,
        keyword: keywords[0]
      })
      
      if (!response.data.error) {
        ElMessage.success('关键词添加成功')
        keywordForm.value.keyword = ''
        await loadKeywords()
        await loadCategories()
      }
    } else {
      // 多个关键词
      const response = await axios.post('/api/keywords/bulk', {
        category_id: keywordForm.value.categoryId,
        keywords: keywords
      })
      
      if (!response.data.error) {
        ElMessage.success(`成功添加 ${response.data.data.inserted} 个关键词`)
        keywordForm.value.keyword = ''
        await loadKeywords()
        await loadCategories()
      }
    }
  } catch (error) {
    console.error('添加关键词失败:', error)
    ElMessage.error('添加关键词失败')
  } finally {
    loading.value = false
  }
}

// 处理Excel文件变更
function handleExcelChange(file) {
  excelForm.value.file = file.raw
  excelForm.value.fileList = [file]
}

// 上传Excel文件
async function uploadExcel() {
  if (!excelForm.value.categoryId) {
    ElMessage.warning('请选择分类')
    return
  }
  
  if (!excelForm.value.file) {
    ElMessage.warning('请选择Excel文件')
    return
  }
  
  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', excelForm.value.file)
    formData.append('category_id', excelForm.value.categoryId)
    
    const response = await axios.post('/api/keywords/upload', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    
    if (!response.data.error) {
      ElMessage.success(response.data.message || '关键词导入成功')
      excelForm.value.file = null
      excelForm.value.fileList = []
      await loadKeywords()
      await loadCategories()
    } else {
      ElMessage.error(response.data.message || '关键词导入失败')
    }
  } catch (error) {
    console.error('上传Excel文件失败:', error)
    ElMessage.error('上传Excel文件失败')
  } finally {
    loading.value = false
  }
}

// 编辑关键词
function editKeyword(keyword) {
  keywordDialog.value.form = {
    id: keyword.id,
    categoryId: keyword.category_id,
    keyword: keyword.keyword
  }
  keywordDialog.value.visible = true
}

// 更新关键词
async function updateKeyword() {
  if (!keywordDialog.value.form.categoryId) {
    ElMessage.warning('请选择分类')
    return
  }
  
  if (!keywordDialog.value.form.keyword) {
    ElMessage.warning('请输入关键词')
    return
  }
  
  keywordDialog.value.loading = true
  try {
    const response = await axios.put(`/api/keywords/${keywordDialog.value.form.id}`, {
      category_id: keywordDialog.value.form.categoryId,
      keyword: keywordDialog.value.form.keyword
    })
    
    if (!response.data.error) {
      ElMessage.success('关键词更新成功')
      keywordDialog.value.visible = false
      await loadKeywords()
      await loadCategories()
    }
  } catch (error) {
    console.error('更新关键词失败:', error)
    ElMessage.error('更新关键词失败')
  } finally {
    keywordDialog.value.loading = false
  }
}

// 确认删除关键词
function confirmDeleteKeyword(keyword) {
  ElMessageBox.confirm(
    `确定要删除关键词 "${keyword.keyword}" 吗？`,
    '删除确认',
    {
      confirmButtonText: '删除',
      cancelButtonText: '取消',
      type: 'warning'
    }
  ).then(() => {
    deleteKeyword(keyword.id)
  }).catch(() => {})
}

// 删除关键词
async function deleteKeyword(keywordId) {
  loading.value = true
  try {
    const response = await axios.delete(`/api/keywords/${keywordId}`)
    
    if (!response.data.error) {
      ElMessage.success('关键词删除成功')
      await loadKeywords()
      await loadCategories()
    }
  } catch (error) {
    console.error('删除关键词失败:', error)
    ElMessage.error('删除关键词失败')
  } finally {
    loading.value = false
  }
}

// 获取分类名称
function getCategoryName(categoryId) {
  const category = categories.value.find(c => c.id === categoryId)
  return category ? category.name : '未分类'
}

// 格式化日期
function formatDate(dateStr) {
  if (!dateStr) return ''
  const date = new Date(dateStr)
  return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`
}
</script>

<style scoped>
.keywords-page {
  padding: 20px;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header-actions {
  display: flex;
  gap: 10px;
}

.filter-btn {
  position: relative;
}

.filter-btn.is-active {
  color: var(--el-color-primary);
  border-color: var(--el-color-primary);
}

.filter-badge {
  position: absolute;
  top: -8px;
  right: -8px;
}

.filter-content {
  padding: 10px 0;
}

.filter-title {
  margin: 0 0 15px 0;
  font-size: 16px;
  color: var(--el-text-color-primary);
}

.filter-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 15px;
  gap: 10px;
}

.category-actions {
  margin-top: 10px;
}

.delete-category-btn {
  width: 100%;
  margin-top: 10px;
}

.upload-section {
  background-color: var(--el-bg-color);
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.05);
  padding: 20px;
  margin-bottom: 20px;
}

.section-header {
  display: flex;
